:host, .uploads-panel {
  flex: auto;
  display: flex;
}

.uploads-panel__file-input {
  display: none;
}

.uploads-panel__file-upload-drop-zone {
  border: dashed #aaa;
  flex-direction: column;
  flex: auto;
  margin: 5px;
  display: flex;
}

.uploads-panel__file-upload-drop-zone_drag-enter {
  background: #00000080;
}

.uploads-panel__uploads-container {
  flex-direction: column;
  display: flex;
}

.uploads-panel__instructions-container {
  flex-direction: column;
  flex: auto;
  justify-content: center;
  align-items: center;
  gap: 1em;
  margin: 5px;
  display: flex;
}

.uploads-panel__instructions-text, .uploads-panel__choose-file-button {
  font-size: xx-large;
  font-weight: bold;
}

